<template>
    <div class="login_box">
        <p class="center_type login_head"></p>
        <section >
            <input type="text" class="login_input" v-model="phone" placeholder="">
            <div class="twoItem">
                <input type="text" class="login_input smscode_input" v-model="smscode" placeholder="">
                <span class="smscode_btn"></span>
            </div>
            <input type="password" class="login_input" v-model="newpass" placeholder="">
            <input type="password" class="login_input" v-model="repass" placeholder="">
        </section>
        <span class="sign_btn forget_btn"></span>
    </div>
</template>

<script type="text/ecmascript-6">
export default {
    data() {
        return {
            phone: '',
            smscode: '',
            newpass: '',
            repass: ''
        }
    },
    methods: {
        
    }
}
</script>

<style media="screen">
.forget_btn{
    margin: 10px auto;
}
</style>


