<template>
  <div>
    <ul class="manage_list_common withdraw_list">
      <li>
        <p></p>
        <input type="text" placeholder="">
      </li>
      <li>
        <p></p>
        <select>
          <option value="1"></option>
        </select>
      </li>
      <li>
        <p></p>
        <input type="text" placeholder="">
      </li>
      <li>
        <p></p>
        <input type="text" placeholder="">
      </li>
      <li>
        <p></p>
        <span class="submit_btn"></span>
        <router-link :to="{name:'wallet'}" class="cancel_btn"></router-link>
      </li>
    </ul>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data(){
      return{
        tabTitle: '',
      }
    },
    mounted(){
      this.$emit('post-title',this.tabTitle);
    }
  }
</script>

<style media="screen">
  .withdraw_list .submit_btn,.withdraw_list .cancel_btn{
    width: 158px;
    margin-right: 10px;
  }
  .withdraw_list li:nth-last-child(2){
    border-bottom: 0;
  }
</style>



